<template>
  <LoadingInfo
    class="content container"
    v-if="this.$store.state.cart.cartLoading || this.$store.state.cart.cartLoadingFailed"
    :condition-loading="this.$store.state.cart.cartLoading"
    :condition-loading-failed="this.$store.state.cart.cartLoadingFailed"
    :condition-reset="loadCart"
    title-loading=" ..."
    title-failed="    "
  />
  <main class="content container" v-else-if="!this.$store.state.cart.cartLoadingFailed">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">  </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">  </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">   </a>
        </li>
      </ul>

      <h1 class="content__title"></h1>
      <ProductInfo :products-count="productsCount" />
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText
              v-model="formData.name"
              :error="formError.name"
              title=""
              placeholder="   "
            />

            <BaseFormText
              v-model="formData.address"
              :error="formError.address"
              title=" "
              placeholder="  "
            />

            <BaseFormText
              v-model="formData.phone"
              :error="formError.phone"
              title=""
              type="tel"
              placeholder="  "
            />

            <BaseFormText
              v-model="formData.email"
              :error="formError.email"
              title="Email"
              type="email"
              placeholder="  Email"
            />

            <BaseFormTextarea
              v-model="formData.comment"
              :error="formError.comment"
              title="  "
              placeholder=" "
            />
          </div>

          <div class="cart__options">
            <h3 class="cart__title"></h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="" />
                  <span class="options__value">  <b></b> </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500" />
                  <span class="options__value">  <b>500 </b> </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title"></h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card" />
                  <span class="options__value">    </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash" />
                  <span class="options__value">    </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <OrderProduct :products="products" :products-count="productsCount" :total-price="this.totalPrice" />

          <button
            class="cart__button button button--primery"
            type="submit"
            v-show="this.$store.state.cart.cartProducts.length"
          >
             
          </button>
        </div>

        <div class="cart__error form__error-block" v-show="formErrorMessage">
          <h4>  !</h4>
          <p>{{ formErrorMessage }}</p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import { mapMutations, mapGetters, mapActions } from 'vuex';
import BaseFormText from '@/components/base/BaseFormText.vue';
import BaseFormTextarea from '@/components/base/BaseFormTextarea.vue';
import ProductInfo from '@/components/product/ProductInfo.vue';
import OrderProduct from '@/components/order/OrderProduct.vue';
import LoadingInfo from '@/components/common/LoadingInfo.vue';
import { orderRequest } from '@/api/order';

export default {
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
    };
  },
  computed: {
    ...mapGetters({
      products: 'cartDetailProducts',
      totalPrice: `cartTotalPrice`,
      productsCount: `cartProductsCount`,
    }),
  },
  methods: {
    ...mapMutations(['productLoadingChecking', 'productLoadingFailedChecking', 'resetCart', 'updateOrderInfo']),
    ...mapActions(['loadCart']),

    order() {
      this.formError = {};
      this.formErrorMessage = '';

      this.productLoadingChecking(true);
      this.productLoadingFailedChecking(false);

      clearTimeout(this.loadProductsTimer);

      this.loadProductsTimer = setTimeout(async () => {
        try {
          const response = await orderRequest(this.$store.state.cart.userAccessKey, this.formData);
          console.log(this.formData);

          this.resetCart();
          this.updateOrderInfo(response.data);
          this.$router.push({ name: 'orderInfo', params: { id: response.data.id } });
        } catch (error) {
          this.formError = JSON.parse(JSON.stringify(error.response.data.error.request)) || {};
          this.formErrorMessage = JSON.parse(JSON.stringify(error.response.data.error.message));
          this.productLoadingFailedChecking(true);
        }
        this.productLoadingChecking(false);
      }, 0);
    },
  },
  components: { BaseFormText, BaseFormTextarea, ProductInfo, OrderProduct, LoadingInfo },
};
</script>

<style>
.total__info {
  font-weight: normal;
  color: #fff;
}
</style>
